@import '../../global.scss';
.menu {
	width: 300px;
	height: 100vh;
	background-color: $mainColor;
	position: fixed;
	top: 0;
	right: -300px;
	z-index: 2;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	transition: all 1s ease;
	// and If active is 'true'
	&.active {
		right: 0px;
	}

	ul {
		margin: 0;
		padding: 0;
		list-style: none;
		font-size: 30px;
		font-weight: 300;
		color: white;
		width: 60%;
		li {
			margin-bottom: 25px;
			a {
				font-size: inherit;
				color: inherit;
				text-decoration: none;
			}
			&:hover {
				font-weight: 500;
			}
		}
	}
}
